@use '../config/_config' as *;
@use 'sass:map';

@forward '../config/_config';

.section-blog {
    &__large-card {
        height: 100%;
        background-color: $colorBgCard;
        color: #ffffff;
    }

    &__large-card-content {
        padding: 10px 0;
    }

    &__large-img-container {
        height: 100%;
        @include spaceImage($maxWidth: 528px, $imgWidth: 1541, $imgHeight: 1080);
    }

    &__large-img {
        @include imgToFillSpace;
        object-fit: cover;
    }

    &__small-card {  
        position: relative;
        padding: 10px;   
        height: 100%;
        // @include bgImg();
        // background-image: url(../../../public/images/small_card-min.jpg);
        color: #ffffff;
    }

    &__small-img {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        font-size: 12px;
        color: map.get($colorPrimary, "regular");

        &--description {
            @extend .section-blog__content;
            color: map.get($colorText, "light");
        }
    }

    &__border {
        position: relative;
        padding: 30px;
        height: 100%;
        border: 1px solid $colorLine;
    }

    &__read-more {
        color: map.get($colorPrimary, "regular");

        &:hover {
            text-decoration: none;
            color: map.get($colorPrimary, "light");
        }
    }
}